import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DataPage } from './data.page';

const routes: Routes = [
  {
    path: 'data',
    component: DataPage,
    children: [
      {
        path: 'world',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('../world/world.module').then(m => m.WorldPageModule)
          }
        ]
      },
      {
        path: 'country',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('../country/country.module').then(m => m.CountryPageModule)
          }
        ]
      },
      {
        path: 'about',
        children: [
          {
            path: '',
            loadChildren: () =>
              import('../about/about.module').then(m => m.AboutPageModule)
          }
        ]
      },
      {
        path: '',
        redirectTo: '/data/world',
        pathMatch: 'full'
      }
    ]
  },
  {
    path: '',
    redirectTo: '/data/world',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DataPageRoutingModule {}
